* {
  box-sizing: border-box;
}
.background {
  font-family: Arial, Helvetica, sans-serif;
  margin: 0;
  height: 100vh;
  width: 100vw;
  background: url("./city.jpeg");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}

/* Zoom In Effect */
.card1zoom {
  animation: zoomIn1 3s;
}
.card2zoom {
  animation: zoomIn2 1.5s;
}
.card3zoom {
  animation: zoomIn3 1.5s;
}

@keyframes zoomIn1 {
  0% {
    transform: scale(1.1);
    transform-origin: 160px 125px;
  }
  50% {
    transform: scale(1.3);
    transform-origin: 320px 250px;
  }

  100% {
    transform: scale(1.5);
    transform-origin: 640px 500px;
  }
}

@keyframes zoomIn2 {
  0% {
    transform: scale(1.1);
    transform-origin: 62.5px 187.5px;
  }
  50% {
    transform: scale(1.3);
    transform-origin: 125px 375px;
  }

  100% {
    transform: scale(1.5);
    transform-origin: 250px 750px;
  }
}

@keyframes zoomIn3 {
  0% {
    transform: scale(1.1);
    transform-origin: 92.5px 146px;
  }
  50% {
    transform: scale(1.3);
    transform-origin: 185px 292px;
  }

  100% {
    transform: scale(1.5);
    transform-origin: 370px 594px;
  }
}